  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.detail-box {
  display: flex;
  justify-content: space-between;
}
.detail-box .zc-introduce-box {
  /*text-align: center;*/
}
.detail-box .zc-introduce-box p:last-of-type {
  margin: 0;
  font-size: 0.48rem;
  color: #4a4a4a;
  margin-left: .6rem;
}
.detail-box .zc-introduce-box .detail {
  display: flex;
  /*justify-content: center;*/
  align-items: center;
}
.detail-box .zc-introduce-box .detail img {
  margin-top: 1px;
  width: .6rem;
}
.detail-box .zc-introduce-box .detail .number {
  font-size: 0.68rem;
  color: #4a4a4a;
  line-height: normal;
  font-weight: bold;
}
.detail-box .zc-introduce-box .detail .yuan {
  margin-top: 2px;
  font-size: 0.36rem;
  color: #39393A;
}
.zc-introduce-box {
  min-width: 33%;
  max-width: 40%;
  text-align: left;
  display: inline-block;
}
.zc-introduce-box1 {
  display: inline-block;
}
.question_box .font-14 {
  line-height: 1rem;
}
.question_box .f-dark {
  line-height: normal;
  margin-bottom: .4rem;
}
.question_box .f-black {
  margin-bottom: .2rem;
}
.commitBtn {
  width: 12.44rem;
  height: 1.6rem;
  line-height: 1.6rem;
  text-align: center;
  background: #FBC873;
  font-size: 0.74rem;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  border-radius: 1.6rem;
  margin: 1rem 0;
}
.newsContent {
  line-height: 1rem;
  word-wrap: break-word;
  word-break: break-all;
  padding: 0 .48rem;
  font-size: 0.56rem;
}
.newsContent img {
  max-width: 100%;
}
.shoucang {
  width: 1.8rem;
  margin-top: 0.4rem;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}
.tagBox {
  list-style: none;
  margin-top: 1rem;
  overflow: hidden;
}
.tagBox li {
  float: left;
  font-size: 0.48rem;
  padding: 0 .55rem;
  height: 1rem;
  line-height: 1rem;
  border: 1px solid #979797;
  border-radius: 1rem;
  margin-left: .48rem;
  margin-top: .2rem;
}
